.header__middle-ad{
	width: 100%;
	float: left;
	display: block;
	background-color: #fff;
	height: 50px;
}
.header__middle-home-left{
	float: left;
	width: 50%;
	display: inline-block;
	padding: 15px 0;
}
.dobavit-home{
	display: block;
	width: 35%;
	float: left;
	margin-right: 15px;
	a{
		float: left;
		text-decoration: none;
		color: #000;
		width: 90%;
		line-height: 18px;
		font-size: 16px;
		font-family: $ob;
		padding-left: 10px;
		position: relative;
	}
}
.header__middle-home-right{
	float: left;
	width: 50%;
	display: inline-block;
}
.home-menu{
	width: 100%;
	float: right;
	display: inline-block;
	list-style-type: none;
	padding-left: 50px;
	margin: 0;
	padding-top: 15px;
	padding-bottom: 15px;
	li{
		display: inline-block;
		float: left;
		margin-left: 40px;
		a{
			font-size: 14px;
			color: #01080a;
			font-family: $ob;
		}

	}
}
.header__bottom-ad{
  width: 100%;
  float:left;
  display: block;
}
.ad-search{
  width: 80%;
  float: left;
  display: block;
  position: relative;
}
.search-pic{
  background-image: url('../img/adsearchpic.png');
  position: absolute;
  background-repeat: no-repeat;
  display: block;
  float: left;
  width: 20px;
  height: 23px;
  z-index: 2;
  left: 25px;
  top: 7px;

}
.input-search-ad{
  float: left;
  display: inline-block;
  height: 37px;
  width: 90%;
  line-height: 37px;
  background-color: #fff;
  border: 1px solid #dedede;
  border-right: 1px solid transparent;
  padding-left: 50px;
  z-index: 1;
  position: relative;
  outline: none;
  font-family: $opi;
  font-size: 16px;
}
.adsearch-button{
  float: left;
  display: inline-block;
  width: 10%;
  background-color: #fff6c4;
  height: 37px;
  line-height: 37px;
  text-align: center;
  text-transform: uppercase;
  font-size: 13px;
  font-family: $osb;
  color: #000;
  text-decoration: none;
  border: 1px solid #dedede;
  &:hover{
    background-color: #fff;
    text-decoration: none;
  }
}
.ad-baner{
  width: 100%;
  float:left;
  height: 48px;
  display: block;
  background-color: #f1f1f1;
}
.adcontent{
  float: left;
  display: block;
  width: 100%;
}
