.addropdown{
 width: 20%;
 padding: 0;
 color: #8c8c8c;
 font-size: 11px;
 float: left;
 display: block;
 height: 35px;
 }
 .addropdown .delivery_list {
 padding: 3px 6px 0 6px;
 height: 35px;
 cursor: pointer;
 margin:0;
 color: #ed4226;
 border: 1px solid #efefef;
 width: 100%;
 z-index: 11;
 background: #fcfcfc;
 
 }
 .delivery_list #btn {
   width: 0;
  	height: 0;
  	border-left: 7px solid transparent;
  	border-right: 7px solid transparent;
  	border-top: 12px solid red;
 float: right;
 }
 .delivery_list span {
 padding: 2px 0 0 2px;
 display: block;
 font-weight: bold;
 }
 .addropdown ul.cities_list {
 margin-top: 0px;
 z-index: 10;
 background: #fcfcfc;
 position: absolute;
 border-radius: 3px;
 width: 20%;
 padding: 0;
 border: 1px solid #eee;
 display: none;
 }
 ul.cities_list li {
 padding: 6px 9px;
 list-style-type: none;
 border-top: 1px solid #efefef;
 }
 ul.cities_list li:hover {
 cursor: pointer;
 background: #fff;
 }
 .addropdown .delivery_text {
 padding: 3px 5px;
 margin: 5px 0 0 3px;
 }
