.header__middle{
	width: 100%;
	float: left;
	padding: 20px 0;
}
.header__middle_left{
	width: 50%;
	float: left;
	strong{
		font-family: $osb;
		font-size: 14px;
	}
}
.header__middle_right{
	width: 50%;
	float: left;
}
.dobavit{
	display: block;
	width: 35%;
	float: right;
	margin-left: 15px;
	a{
		float: left;
		text-decoration: none;
		color: #000;
		width: 90%;
		line-height: 18px;
		font-size: 16px;
		font-family: $ob;
		padding-left: 10px;
		position: relative;
	}
}
.icon-plus{
	float: left;
	display: block;
	height: 16px;
	width: 10%;
	background-image: url('/img/icon_plus.png');
	background-repeat: no-repeat;
}
.header__bottom{
	width: 100%;
	float: left;
/* 	padding: 20px 0; */
}
.category{
	width: 20%;
	height: 85px;
	float: left;
	background-color: #ffd800;
}
.category-item{
	width: 197px;
	display: block;
	height: 32px;
	border: 1px solid rgba(10, 1, 1, 0.51);
	border-radius: 13px;
	margin: 26px auto;
    float: none;
    text-align: center;
    cursor: pointer;
    span{
    	font-size: 14px;
    	font-family: $ob;
    	color: #000;
    	text-decoration: none;
    	text-align: center;
    	top: 5px;
    	position: relative;
    }
}
.category-icon{
	width: 17px;
	height: 17px;
	background-image: url('/img/category_icon.png');
	background-repeat: no-repeat;
	padding-left: 20px;
}
.menu{
	width: 80%;
	float: left;
	background-image: url('/img/bottom_fon.png');
	height: 85px;
	ul{
		text-align: right;
		float: left;
		width: 100%;
		list-style: none;
	    padding-left: 0;
	    padding: 32px 0;
    	margin-bottom: 0;
		li{	
			padding-right: 10px;
			a{
				text-decoration: none;
				color: #000;
				font-family: $ob;
				font-size: 14px;
				margin-right: 35px;
				&:last-of-type{
					margin-right: 0;
				}
			}
		}
	}
}

.content{
	width: 100%;
	float: left;
	display: block;
}
.right{
	width: 80%;
	float: left;
	display: block;
	position: relative;
	border-left: 1px solid #DEDEDE;
}
h2{
	font-size: 18px;
	font-family: $ob;
	width: 100%;
	float: left;
	padding-left: 10px;
}
.left{
	width: 20%;
	float: left;
	display: block;
	text-align: center;
	
}
.left-menu{
	width: 80%;
	padding: 20px;
	list-style: none;
	display: block;
	margin: 0 auto;
    float: none;
    text-align: left;
	li{
		margin: 10px 0;
		a{
			text-decoration: none;
			color: #cdb849;
			font-family: $ob;
			font-size: 14px;
		}
	}
}
.content-forma{
	width: 65%;
	float: left;
	display: block;
	margin: 0 auto;
	text-align: center;
	padding-left: 100px;
	padding-top: 20px;
	padding-bottom: 20px;
}
.form-line{
	width: 100%;
	float: left;
	display: block;
	position: relative;
}
.check-category{
	width: 70%;
	display: block;
	float: left;
}
.check-thumb{
	float: left;
	display: block;
	width: 14%;
	height: 55px;
	position: relative;
	img{
		max-width: 100%;
		position: absolute;
	    top: 50%;
	    left: 50%;
	    -webkit-transform: translate(-50%, -50%);
	       -moz-transform: translate(-50%, -50%);
	        -ms-transform: translate(-50%, -50%);
	         -o-transform: translate(-50%, -50%);
	            transform: translate(-50%, -50%);	
	}
}
.check-title{
	width: 84%;
	display: block;
	float: left;
	font-family: $or;
	font-size: 11px;
	line-height: 44px;
}
.memo{
	display: block;
	width: 270px;
	background: #e8f4de;
    color: #528425;
    height: auto;
    float: right;
    position: absolute;
    right: -320px;
    top: 10px;
    z-index: 10;
    padding: 7px 10px 7px 40px;
    -webkit-border-radius: 5px;
            border-radius: 5px;
    p{
    	text-align: left;
    }
}
.info-icon{
	width: 20px;
	display: block;
	height: 20px;
	background-image: url('/img/info.png');
	background-repeat: no-repeat;
	z-index: 12;
	background-size: contain;
	position: absolute;
	left: 10px;
    top: 10px;
}
.triangle-left{
	width: 0;
	height: 0;
	border-top: 7px solid transparent;
    border-right: 11px solid #e8f4de;
    border-bottom: 7px solid transparent;
    position: absolute;
    float: left;
    left: -11px;
    top: 10px;
}
.memo-error{
	background-color: #c91414;
    color: #fff;
    padding-left: 10px;
    width: 270px;
    position: absolute;
    z-index: 2;
    float: right;
    right: -215px;
    top: 10px;
    -webkit-border-radius: 5px;
            border-radius: 5px;
    .triangle-left{
    	 border-right: 11px solid #c91414;
    }
}
.calc{
	color: #1a97bb;
	float: left;
	width: 82%;
	margin-bottom: 0;
}
.label-name{
	width: 30%;
	float: left;
	display: block;
	font-size: 13px;
	font-family: $or;
	text-align: left;	
	position: relative;
	top: 17px;
	color: #000;
	right: 0px;
	span{
		color: #ff8b02;
	}
}
.input-name{
	display: block;
	width: 70%;
	float: left;
	line-height: 30px;
	border: 1px solid #b8b6b6;
	margin: 12px 0;
	height: 30px;
	padding-left: 10px;
	margin-bottom: 0;
}
.input-small{
	display: block;
	width: 50%;
	float: left;
	line-height: 30px;
	border: 1px solid #b8b6b6;
	margin: 12px 0;
	height: 30px;
	padding-left: 10px;
}
.area-name{
	width: 70%;
	height: 80px;
	border: 1px solid #b8b6b6;
	padding: 0;
	resize: none;
	overflow: auto;
	margin: 12px 0;
	padding-left: 10px;
	margin-bottom: 0;
}
.file_upload{
    display: block;
    position: relative;
    overflow: hidden;
    font-size: 1em;
    height: 40px;
    line-height: 40px;
    width: 70%;
    left: 0;
    bottom: 0;
	border-radius: 1px;
    &:last-of-type {
		margin-right: 0;
	}
}
.file_upload .button, .file_upload > mark{
    display: block;
    cursor: pointer;
}

.button{
	top: 10px;
	height: 30px;
	width: 40px;
    float: left;
    box-sizing: border-box;
    -moz-box-sizing: border-box;
    width: 100%;
    height: 100%;
    text-align: center;
    color: #fff;
    left: 0;
    font-size: 17px;
    /* font-family: $ql; */
    position: relative;
    background-image: url('/img/icon_camera.png');
    background-repeat: no-repeat;
}
.input-file{
    display: none;
}
input[type=file] {
    display: none;
}
.content-dannie{
	width: 100%;
	float: left;
	display: block;
	position: relative;
	text-align: center;
	padding: 20px 0;
	span{
		width: 50%;
		float: none;
		margin: 0 auto;
		position: relative;
		display: block;
		padding: 5px 0;
		p{
			text-align: left;
		}
		input{
			display: none;
		}
		label{
			height: 16px;
			width: 16px;
			/* background-color: #FFF; */
			border: 1px solid #b8b6b6;
			position: absolute;
    		left: -22px;
		}
		input[type=checkbox]:checked + label{
			background-image: url('/img/check.png');
			background-repeat: no-repeat;
			height: 14px;
			display: block;
			-webkit-background-size: contain;
			     -o-background-size: contain;
			        background-size: contain;
			width: 14px;
		}
	}
	.publish{

		font-size: 14px;
		font-family: $or;
		width: 30%;
    	font-weight: 600;
	    border-radius: 5px;
	    line-height: 30px;
		background-color: #ffd800;
		text-decoration: none;
		text-transform: uppercase;
		color: #000;
		height: 30px;
		display: inline-block;
	}
	.prew{
		font-size: 14px;
		color: #000;
		display: inline-block;
		width: 24%;
	}
	.soc-icon{
		background-image: url('/img/vk-icon.png');
		background-repeat: no-repeat;
		width: 26px;
		height: 26px;
		float: right;
		position: absolute;
	    display: inline-block;
	    top: 0px;
    	right: 0;
		-webkit-background-size: contain;
		     -o-background-size: contain;
		        background-size: contain;
	}
}
