
.home-baner{
	height: 150px;
	width: 100%;
	float: left;
	display: block;
	position: relative;
	text-align: center;
	padding: 30px 0;
}
.baner{
	height: 90px;
	background-color: #f1f1f1;
	width: 50%;
	margin: 0 auto;
}
.header__bottom-home{
	width: 100%;
	float: left;
	display: inline-block;
}
.header__bottom-home-left{
	float: left;
	display: block;
	width: 20%;
	.category-item{
		margin: 0;
		border: 2px solid #000;
		height: 34px;
		border-radius: 13px;
	}
}
.header__bottom-home-right{
	float: left;
	width: 80%;
	display: block;

}
.input-search{
	width: 85%;
	float: left;
	display: inline-block;
	height: 34px;
	line-height: 34px;
	background-color: #fff;
	border: 2px solid #ffd800;
	border-radius: 15px;
	padding-left: 20px;
	z-index: 1;
	position: relative;
	left: 22px;
	outline: none;
}
.button-search{
	width: 15%;
	float: left;
	display: inline-block;
	height: 34px;
	background-color: #ffd800;
	border-radius: 15px;
	font-size: 16px;
	font-family: $osb;
	text-align: center;
	line-height: 35px;
	color: #000;
}
.home-content{
	float: left;
	width: 100%;
	display: block;
}
.home-contents{
	float: left;
	width: 100%;
	display: flex;
	-webkit-flex-wrap: wrap;
	    -ms-flex-wrap: wrap;
	        flex-wrap: wrap;
	padding-top: 35px;
	flex-direction: row;
}
.home-content-item{
	-webkit-flex-basis: 50%;
	        flex-basis: 50%;
	display: flex;
	margin: 25px 0;
	width: 50%;
}
.content-item-thumb{
	display: block;
	position: relative;
	width: 15%;
	height: 55px;
	float: left;
	img{
		max-width: 100%;
	    /* min-height: 100%; */
		position: absolute;
	    top: 50%;
	    left: 50%;
	    -webkit-transform: translate(-50%, -50%);
	       -moz-transform: translate(-50%, -50%);
	        -ms-transform: translate(-50%, -50%);
	         -o-transform: translate(-50%, -50%);
	            transform: translate(-50%, -50%);
	}
}
.content-item-text{
	float: left;
	display: block;
	width: 85%;
	padding-left: 30px;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
	        box-sizing: border-box;
}
.text-title{
	color: #000304;
	font-size: 18px;
	font-family: $osb;
	text-align: left;
	display: block;
	padding-bottom: 10px;
	&:hover{
		color: #ffd800;
	}
}
.text-about{
	color: #616161;
	font-size: 14px;
	font-family: $osb;
	text-align: left;
}
.home-slider{
	width: 100%;
	float: left;
	display: inline-block;
	text-align: center;
	position: relative;
	background-color: #FFF6C9;
	padding: 30px 0;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
	        box-sizing: border-box;
	h3{
		font-size: 16px;
		text-align: left;
		font-family: $osb;
		margin-bottom: 20px;
		margin-top: 0;
		width: 50%;
		float: left;
		display: inline-block;
	}
	a{
		float: right;
		font-size: 16px;
		font-family: $opl;
		color: #000;
		display: inline-block;
		margin-bottom: 20px;
		text-align: right;
	}
}

.owl-model{
	width: 90% !important;
	float: none;
	display: inline-block;
	margin: 0 auto;
	position: relative;
	margin-bottom: 20px;
}
.owl-stage-outer{
	width: 100%;
}
.owl-controls {
display: block;
	.owl-nav{
		position: absolute;
	    top: 0;
	    width: 100%;
	    .owl-prev{
	    	background-image: url("../img/left-arrow.png");
		    left: -50px;
	    }
	    .owl-next{
		    right: -50px;
		    background-image: url(../img/right-arrow.png);
	    }
	    .owl-prev,.owl-next{
	    	width: 32px;
		    height: 32px;
		    position: absolute;
		    top: 80px;
		    padding: 0;
		    background-repeat: no-repeat;
	    }
	}
}
.slide{
	float: left;
	width: 100%;
	display: block;
	p{
		font-family: $ob;
		font-size: 14px;
		text-align: left;
	}
}
.slide-link{
	width: 100%;
	img{
		height: 100px;
		width: 100%;
	}
	h4{
		font-size: 12px;
		font-family: $ob;
		color: #1a97bb;
		text-align: left;
		text-decoration: underline;
		margin-bottom: 5px;
	}
}
.new-organization{
	width: 100%;
	float: left;
	display: block;
	padding: 30px 0;
	text-align: center;
	position: relative;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
	        box-sizing: border-box;
	h3{
		font-size: 16px;
		text-align: left;
		font-family: $osb;
		margin-bottom: 20px;
		margin-top: 0;
		width: 50%;
		float: left;
		display: inline-block;
	}
	a{
		float: right;
		font-size: 16px;
		font-family: $opl;
		color: #000;
		display: inline-block;
		margin-bottom: 20px;
		text-align: right;
		width: 50%;
	}
}
.org{
	float: none;
	margin: 0 auto;
	width: 90%;
}
.org-items{
	float: left;
	width: 13.3%;
	display: inline-block;
	margin-left: 42px;
	.slide-link{
		width: 100%;
		float: left;
		display: block;
	}
	&:first-of-type{
		margin-left: 0;
	}
}
.specials{
	width: 100%;
	float: left;
	display: inline-block;
	text-align: center;
	position: relative;
	background-color: #FFF6C9;
	padding: 30px 0;
	-webkit-box-sizing: border-box;
	   -moz-box-sizing: border-box;
	        box-sizing: border-box;
	h3{
		font-size: 16px;
		text-align: left;
		font-family: $osb;
		margin-bottom: 20px;
		margin-top: 0;
		width: 50%;
		float: left;
		display: inline-block;
	}
	a{
		float: right;
		font-size: 16px;
		font-family: $opl;
		color: #000;
		display: inline-block;
		margin-bottom: 20px;
		text-align: right;
		width: 50%;
	}
}
.specials-row{
	float: none;
	margin: 0 auto;
	width: 90%;
}
.specials-items{
	width: 24%;
	float: left;
	display: inline-block;
	border: 4px solid transparent;
	margin-right: 10px;
	&:hover{
		border: 4px solid #FFD800;
		.vip{
			display: block;
			position: absolute;

		}
		.offer{
			display: block;
		}
	}
	&:last-of-type{
		margin-right: 0;
	}

}
.percent{
	top: 0;
	display: block;
	position: absolute;
	width: 45px;
	height: 20px;
	float: left;
	left: 15px;
	font-size: 15px;
	font-family: $hbl;
	text-align: center;
	line-height: 20px;
	color: #FFF;
	background-color: #000;
	&:before{
		content: "";
		width: 0;
		height: 0;
		border-left: 22px solid transparent;
		border-right: 22px solid transparent;
		border-top: 15px solid #000;
		position: absolute;
		bottom: -15px;
		left: 0;
	}
}
.vip{
	top: 0;
	display: none;
	z-index: 10;
	position: absolute;
	width: 45px;
	height: 20px;
	float: right;
	background-color: #FFD800;
	right: 15px;
	font-size: 15px;
	font-family: $hbl;
	text-align: center;
	line-height: 20px;
	&:before{
		content: "";
		width: 0;
		height: 0;
		border-left: 22px solid transparent;
		border-right: 22px solid transparent;
		border-top: 15px solid #FFD800;
		position: absolute;
		bottom: -15px;
		left: 0;
	}
}
.offer{
	width: 100%;
	height: 65%;
	float: left;
	display: none;
	position: absolute;
	z-index: 10;
	background-color: rgba(0, 0, 0, 0.75);
	padding: 10px 0px 10px 10px;
	bottom: 0;
	p{
		font-size: 15px;
		text-align: left;
		font-family: $hl;
		color: #FFF;
		margin-bottom: 5px;
	}
	.more-offer{
		display: inline-block;
		font-size: 15px;
		font-family: $hl;
		color: #000;
		text-transform: uppercase;
		text-align: center;
		width: 50%;
		background-color: #debd2a;
		border-radius: 2px;
		float: left;
		padding: 0 10px;
		-webkit-box-sizing: border-box;
		   -moz-box-sizing: border-box;
		        box-sizing: border-box;
	}
}

.specials-items-thumb{
	width: 100%;
	height: 170px;
	position: relative;
	text-align: center;
	display: block;
	@include overflow-img;
	background-color: #FFF;
	img{
		max-width: 150%;
	}
	}
.specials-items-title{
	width: 100%;
	float: left;
	display: block;
	background-color: #FFF;
	height: 40px;
	h3{
		width: 100%;
		font-size: 13px;
		font-family: $hl;
		margin: 0;
		padding: 5px 0 5px 5px;
		border-bottom: 1px solid #d1d1d1;
	}
	p{
		width: 100%;
		float: left;
		display: inline-block;
		height: 16px;
		font-size: 10px;
		font-family: $hl;
		text-align: left;
		padding-left: 15px;
		text-decoration: line-through;
		line-height: 16px;
		margin: 0;
		background-color: #FFF;

	}
}
.yellow-triangle{
	display: block;
    width: 40%;
    float: right;
 	background-color: #FFD800;
    font-size: 12px;
    font-family: $hbl;
    position: relative;
    text-align: right;
    padding-right: 10px;
    height: 16px;
    &:before{
    	content: "";
    	display: block;
    	position: absolute;
    	width: 0;
		height: 0;
		border-top: 16px solid #FFD800;
		border-left: 16px solid transparent;
		left: -15px;
		float: left;
    }

}
