.header__middle-home{
	width: 100%;
	float: left;
	display: block;
	background-color: #fff6c4;
	height: 50px;
}
.header__middle-home-left{
	float: left;
	width: 50%;
	display: inline-block;
	padding: 15px 0;
}
.dobavit-home{
	display: block;
	width: 35%;
	float: left;
	margin-right: 15px;
	a{
		float: left;
		text-decoration: none;
		color: #000;
		width: 90%;
		line-height: 18px;
		font-size: 16px;
		font-family: $ob;
		padding-left: 10px;
		position: relative;
	}
}
.header__middle-home-right{
	float: left;
	width: 50%;
	display: inline-block;
}
.home-menu{
	width: 100%;
	float: right;
	display: inline-block;
	list-style-type: none;
	padding-left: 50px;
	margin: 0;
	padding-top: 15px;
	padding-bottom: 15px;
	li{
		display: inline-block;
		float: left;
		margin-left: 40px;
		a{
			font-size: 14px;
			color: #01080a;
			font-family: $ob;
		}

	}
}
