.header__top{
	width: 100%;
	float: left;
	display: block;
	padding: 20px 0;
	border-bottom: 1px solid #DEDEDE;
}
.line{
	&:hover{
		border-bottom: 1px solid #1a97bb;
	}
}
.header__top_left, .header__top_right{
	float: left;
	display: block;
	width: 50%;
}
.header__top_logo{
	width: 20%;
	float: left;
}
.header__top_count-ad{
	width: 80%;
	float: left;
	text-align: left;
	p{
		font-size: 14px;
		margin: 0;
		font-family: $or;
		color: #000;
		a{
			text-decoration: none;
			text-transform: uppercase;
			font-family: $oeb;
			color: #bca008;
			border-bottom: 1px solid #1a97bb;
			cursor: pointer;
		}
	}
}
 .header__top_advert, .header__top_price{
	width: 25%;
	float: left;
	color: #000;
	text-decoration: none;
	font-size: 14px;
	font-family: $or;
}
.header__top_favorites{
	width: 20%;
	color: #000;
	float: left;
	text-decoration: none;
	font-size: 14px;
	font-family: $or;
}
.header__top_price{
	padding-left: 10px;
	width: 30%;
	font-weight: 900;
}
.circle{
	position: absolute;
	width: 13px;
	height: 12px;
	background-color: #f78900;
	border-radius: 6px;
	float: right;
	display: inline-block;
	right: -4px;
    top: -3px;
    font-size: 11px;
    color: #FFF;
    font-family: $ob;
    text-align: center;
    line-height: 12px;
  
}
.header_top_icon{
	height: 18px;
	width: 18px;
	background-image: url('../img/sprite.png');
	background-repeat: no-repeat;
	float: left;
	margin-right: 10px;
	position: relative;
}
.favorites_icon{
	background-image: url('../img/sprite.png');
	background-position: 0px 0px;
	width: 18px;
	height: 18px;
	cursor: pointer;
}
.favorites_icon_press{
	background-image: url('../img/sprite.png');
	background-position: -18px 0px;
	width: 18px;
	height: 18px;
	cursor: pointer;
}
.advert_icon{
	background-image: url('../img/sprite.png');
	background-position: 0px -18px;
	 width: 17px;
  height: 15px;
	cursor: pointer;
}
.advert_icon_press{
	background-image: url('../img/sprite.png');
	background-position: -17px -18px;
	 width: 17px;
  height: 15px;
	cursor: pointer;
}
.price_icon{
	width: 22px;
	height: 19px;
	background-image: url('../img/price.png');
	float: left;
	background-repeat: no-repeat;
	cursor: pointer;
	margin-right: 10px;
}
.header__top_user{
	width: 25%;
	float: right;
}
.header_user{
	width: 100%;
	float: right;
	
	
}

.user-pic{
	width: 31px;
	height: 31px;
	float: left;
	position: relative;
	margin-right: 10px;
	bottom: 5px;
	img{
		position: absolute;
	    top: 50%;
	    left: 50%;
	    -webkit-transform: translate(-50%, -50%);
	       -moz-transform: translate(-50%, -50%);
	        -ms-transform: translate(-50%, -50%);
	         -o-transform: translate(-50%, -50%);
	            transform: translate(-50%, -50%);	
	}
}
.user-name{
	float: left;
	font-size: 16px;
	font-family: $ob;
	color: #000;
	border-bottom: 1px solid #1a97bb;
	
}



